import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

class OrderList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      orders: [],
      deletedOrders: [],  //      
      error: null,
      viewingDeleted: false, //    
    };
  }

  componentDidMount() {
    this.fetchOrders();
  }

  // 
  fetchOrders() {
    axios.get('https://server-6kol.onrender.com/api/v1/orders/')
      .then(response => {
        this.setState({ orders: response.data });
      })
      .catch(error => {
        this.setState({ error });
      });
  }

  // 
  fetchDeletedOrders = () => {
    axios.get('https://server-6kol.onrender.com/api/v1/orders/deleted/')
      .then(response => {
        this.setState({ deletedOrders: response.data, viewingDeleted: true });
      })
      .catch(error => {
        this.setState({ error });
      });
  }
  //  
  switchToNormalView = () => {
    this.setState({ viewingDeleted: false });
  }

  handleDownloadExcel = () => {
    axios({
      url: 'https://server-6kol.onrender.com/api/v1/orders/downloads/',
      method: 'GET',
      responseType: 'blob', // :  blob   
    }).then((response) => {
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', 'data.xlsx'); //    
      document.body.appendChild(link);
      link.click();
    }).catch(error => {
      console.error('   :', error);
    });
  }

  deleteOrder = (orderId) => {
    axios.delete(`https://server-6kol.onrender.com/api/v1/orders/${orderId}/`)
      .then(response => {
        //     
        this.fetchOrders();
      })
      .catch(error => {
        console.error('    :', error);
      });
  }

  // sheet 
  handleUpdateSpreadsheet = () => {
    axios.get('https://server-6kol.onrender.com/api/v1/orders/updategs/')
      .then(response => {
        console.log('  :', response);
        alert('  .');
      })
      .catch(error => {
        console.error('    :', error);
        alert('    .');
      });
  }
  

  render() {
    const { orders, deletedOrders, error, viewingDeleted } = this.state;

    if (error) {
      return <div> : {error.message}</div>;
    }

    return (
      <div>
        <h2>
          <Link to={'/orders/new'}>  </Link></h2>
        <h2> </h2>
        <button onClick={this.handleDownloadExcel}> </button>
        <button onClick={this.handleUpdateSpreadsheet}> </button>
        {!viewingDeleted && <button onClick={this.fetchDeletedOrders}></button>}
        {viewingDeleted && <button onClick={this.switchToNormalView}></button>}
        <ul style={{ listStyleType: 'none', padding: 0 }}>
          {(viewingDeleted ? deletedOrders : orders).map(order => (
            <li key={order.orderNumber} style={{ display: 'flex', marginBottom: '10px' }}>
              <div style={{ marginRight: '20px' }}>
                <Link to={`/orders/${order.id}`}> : {order.orderNumber}</Link>
              </div>
              <div style={{ marginRight: '20px' }}> : {order.ordererName}</div>
              <div style={{ marginRight: '20px' }}>: {order.affiliation}</div>
              <div style={{ marginRight: '20px' }}>: {order.contact}</div>
              <div style={{ marginRight: '20px' }}> : {order.orderStatus}</div>
              <div style={{ marginRight: '20px' }}>: {order.creator}</div>
              <div style={{ marginRight: '20px' }}> : {order.creationTime}</div>
              <div style={{ marginRight: '20px' }}>: {order.totalAmount}</div>
              <div>: {order.balance}</div>
              {!viewingDeleted && (
                <button onClick={() => this.deleteOrder(order.id)} style={{ marginLeft: '20px' }}>
                  
                </button>
              )}
            </li>
          ))}
        </ul>
      </div>
    );


  }
}

export default OrderList;
